<template>
    <div>
        <mhead-top titleHead="发表评价" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/order/order" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div class="meva-pro">
                <div class="m-eva-item">
                    <div class="mev-pic">
                        <el-image class="umev-image" :src="info.goods[0].pic" fit="cover" :preview-src-list="Array(info.goods[0].pic)"></el-image>
                    </div>
                    <div class="mev-info">
                        <div class="mev-name">{{info.goods[0].name}}</div>
                        <div class="mev-spec w">
                            <span>规格：{{info.goods[0].spec_name}}{{info.goods[0].spec_unit}}</span>
                        </div>
                        <div class="mev-spec w">
                            <span>￥{{info.goods[0].price}}/{{info.goods[0].supply_unit}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mev-form">
                <div class="mev-f-box">
                    <form>
                        <div class="meva-score">
                            <span>评分</span>
                            <div class="meva-star">
                                <el-rate
                                    v-model="form.goodsRank"
                                    show-score
                                    text-color="#ff9900">
                                </el-rate>
                            </div>
                        </div>
                        <div class="pb10">
                            <el-input
                                    type="textarea"
                                    maxlength="1000"
                                    show-word-limit
                                    :autosize="{ minRows: 4, maxRows: 6}"
                                    placeholder="请输入内容"
                                    v-model="form.content">
                            </el-input>
                        </div>
                        <div class="meva-pic">
                            <el-upload
                                class="picUploader"
                                action="http://api.sixmol.com/api/v1/uploadComment"
                                :limit="5"
                                :on-exceed="handleExceed"
                                list-type="picture-card"
                                :on-change="handleChange"
                                :on-preview="handlePictureCardPreview"
                                :on-remove="handleRemove">
                                <i slot="default" class="el-icon-plus"></i>
                            </el-upload>
                            <el-dialog :visible.sync="dialogVisible" class="pr">
                                <img width="100%" :src="dialogImageUrl" alt="">
                                <i class="el-icon-error delIcon"></i>
                            </el-dialog>
                        </div>
                    </form>

                </div>
            </div>
            <div class="meva-subit" @click="confirm()">
                <span class="tc w">提交评价</span>
            </div>
        </div>
    </div>
</template>


<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {addComment,uploadComment,getOrderInfo} from '../../../service/api';
    export default {
        name: 'mEvaluate',
        data(){
            return{
                order_id:'',
                info:'',
                form:{
                    goodsId:'',
                    speId:'',
                    content: '',
                    goodsRank:5,
                    photos:[]
                },
                fileList:[],
                dialogImageUrl: '',
                dialogVisible: false
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{
        },
        methods:{
            async initData(){
                this.order_id = this.$route.query.order_id || '';
                var res = await getOrderInfo(this.order_id);
                this.info = res;

                this.form.goodsId = res.goods[0].goods_id;
                this.form.speId = res.goods[0].spec_id;
            },
            
            handleChange(file, fileList) {
                this.calculateFileList(fileList);
            },
            handleRemove(file, fileList) {
                this.calculateFileList(fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;

            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制只能选择上传 5 张图片`);
            },
            async confirm(){
                if(this.form.content==''){
                    this.$message({
                        message:'评价内容不能为空',
                        type:'warning'
                    });
                }else{
                    var res = await addComment(
                        this.form.content,
                        this.form.goodsId,
                        this.form.speId,
                        this.order_id,
                        this.form.goodsRank,
                        this.form.photos
                    );
                    this.$message({
                        message:res.msg,
                        type:res.status==12000?'success':'warning'
                    });
                    var self = this;
                    setTimeout(function(){
                        self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.order_id}});
                    },1000);
                }
            },
            calculateFileList(fileList){
                var arr = [];
                for(var i =0 ; i < fileList.length ; i++){
                    arr[i] = fileList[i].response;
                }
                this.form.photos = arr;
            }

        },
        created(){


        },

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    /*产品*/
    .meva-pro{
        padding:px2rem(20) px2rem(20) 0 px2rem(20);
        background: #fff;
    }
    .m-eva-item{
        background: #f5f5f5;
        padding:px2rem(10);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
        height: px2rem(120);
    }
    // .mev-pic{
    //     width:px2rem(120);
    //     height: px2rem(120);
    //     padding-right:px2rem(20);
    // }
    .umev-image{
        width:px2rem(120);
        height: px2rem(120);
        background-color: #fff;
        margin-right: px2rem(20);
    }
    .mev-info{
        width:px2rem(510);
    }
    .mev-name{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        color:#333;
        line-height: px2rem(46);
        min-width: px2rem(460);
    }
    .mev-spec{
        font-size:px2rem(22);
        color:#999;
        display: block;
        border-right: 3px;
        flex-wrap: nowrap;
        height: px2rem(36);
        line-height: px2rem(36);
    }
    .mev-spec span{
        display: block;
        float:left;
    }
    .mev-spec .icon{
        font-size:px2rem(22);
        color:#999;
        height: px2rem(36);
        line-height: px2rem(36);
        padding-left:px2rem(6);
        float:left;
    }

    /*表单*/
    .mev-form{
        background-color: #fff;
        padding-bottom:px2rem(20);
    }
    .mev-f-box{
        padding:0 px2rem(20);
    }
    /*评分*/
    .meva-score{
        display: flex;
        flex-wrap: nowrap;
        padding:px2rem(20) 0;
    }
    .meva-star{
        padding:px2rem(10) 0 0 px2rem(30)
    }
    /*图片*/
    // .meva-pic{
    //     padding:px2rem(20) 0;
    // }
    // .meva-img,.meva-pic .el-upload-list__item.is-ready{
    //     width:px2rem(122);
    //     height: px2rem(122);
    // }
    // .meva-pic .el-upload--picture-card{
    //     width:px2rem(122);
    //     height: px2rem(122);
    //     line-height: px2rem(126);
    // }
    .picUploader .el-upload--picture-card {
        width: 62px;
        height: 62px;
        line-height: 62px;
    }

    .picUploader .el-upload-list--picture-card .el-upload-list__item {
        width: 62px;
        height: 62px;
    }

    .picUploader .el-upload-list--picture-card .el-upload-list__item-thumbnail {
        width: 62px;
        height: 62px;
    }

    .meva-subit{
        width:100%;
        height: px2rem(104);
        position: fixed;
        bottom:0;
        left:0;
        border-top:1px solid #eee;
        z-index: 1000;
        display: flex;
        flex-wrap: nowrap;
        line-height: px2rem(104);
        background-color: $default;
        color:#fff;
        text-align: center;
    }
    .delIcon{
        position: absolute;
        top:0;
        right:0;
        color:#ff4400;
        z-index:1000;
    }


</style>



